body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

a:hover {
  opacity: 0.9;
}

a:active {
  opacity: 1;
}

.li {
  list-style: none;
}

.movies {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  grid-gap: 10px;
  margin-left: 50px;
  margin-top: 40px;
}
.movies .li {
  position: relative;
}
.movies .li img {
  width: 100%;
}
.movies .li .p {
  /* font-size: 12px; */
  padding: 10px;
  background: linear-gradient(
    to bottom,
    rgba(0, 0, 9, 0) 0%,
    rgba(0, 0, 0, 0.9) 100%
  );
  position: absolute;
  text-align: center;
  top: 0;
  width: 100%;
  display: none;
  transition: 1s;
}
.movies .li:hover .p {
  transition: 1s;
  display: block;
}
