.footer_section {
  display: flex;
  flex-direction: row;
  padding: 5% 10%;
  width: 100vw;
  margin-top: 30px;
  background-color: #172146;
}
h5 {
  margin-top: 10px;
  font-size: 18px;
  margin-bottom: 10px;
  color: hsla(0, 0%, 100%, 0.5);
}
.edyoda {
  display: flex;
  flex-direction: column;
  width: 30%;
}
.edyoda a {
  text-decoration: none;
  margin-top: 5px;
  color: #fff;
}

.resources {
  display: flex;
  flex-direction: column;
  width: 30%;
}
.resources a {
  text-decoration: none;
  margin-top: 5px;
  color: #fff;
}

.contact {
  display: flex;
  flex-direction: column;
}

.contact a {
  text-decoration: none;
  margin-top: 5px;
  color: #fff;
}

.icons i {
  margin: 5px;
  margin-right: 20px;
  font-size: 23px;
  color: #fff;
  cursor: pointer;
}
